@import url(https://fonts.googleapis.com/css?family=Questrial|Varela+Round);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1, h2, h3, h4, h5, h6{
    color: #cccccc;
    font-family: 'Varela Round', sans-serif;
}

body {
    color: #cccccc;
    font-family: 'Questrial', sans-serif;
} 

h1 {
    color: #8156bc !important;
}

h2 {
    font-size: 2em;
    background: -webkit-gradient(linear, left top, right top, from(#8156bc), to(#e06b6b));
    background: linear-gradient(to right, #8156bc 0%, #e06b6b 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.Presentation {
    height: 95vh;
    background-color: #191919;
    padding-left: 0 !important;
    padding-right: 0 !important;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between
}

.Presentation__name-area {
    margin-top: 20vh;
    padding-left: 5%;
}

.Presentation__name-area__svg {
    margin-bottom: -19em;
    z-index: -1; 
    margin-left: 0%;
    padding-left: 3%;
    width: 530px;
    height: 20px;
    -webkit-animation-name: appering;
            animation-name: appering;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
}

@-webkit-keyframes appering {
    0%{
        width: 0px;
    }
    100%{
        width: 530px;
    }
}

@keyframes appering {
    0%{
        width: 0px;
    }
    100%{
        width: 530px;
    }
}

.Presentation__name-area__svg__rectangle {
    width: 800px;
    height: 20px;
    z-index: -1; 
}

.Presentation__name-area__names__name {
    width: 540px;
}

.Presentation__name-area__names__name--first {
    font-size: 12em;
    margin: 0;
    color: #cccccc !important;
    font-weight: 200;
    text-align: left;
    line-height: 1em;
}

.Presentation__name-area__names__name--last {
    font-size: 12em;
    margin: 0;
    color: #cccccc !important;
    font-weight: 200;
    text-align: right;
    line-height: 1em;
}


.Presentation__name-area__names__description {
    margin: 0 auto;
    height: 1em;
    font-size: 3em;
    
}

.Presentation__image {
    margin-top: 18vh;
    margin-right: 10%;
    width: 400px;
    height: 518px;
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

@-webkit-keyframes grow {
    0%{
        margin-right: -25%;
    }
    100%{
        margin-right: 10%;
    }
}

@keyframes grow {
    0%{
        margin-right: -25%;
    }
    100%{
        margin-right: 10%;
    }
}

@media (max-width:1087px) {
    .Presentation {
        height: 100%;
    } 

    .Presentation__name-area {
        margin: auto;
        margin-top: 4em;
        margin-bottom: 3em;
    }

    .Presentation__image {
        padding-bottom: 10%;
        padding-top: 5em;
        margin: auto;
    }
}

@media (max-width: 580px) {

    .Presentation {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    .Presentation__name-area__names {
        width: 380px;
        margin: auto;
    }
    
    .Presentation__name-area__names__name--first {
        font-size: 8em;
        margin: 0;
        font-weight: 200;
        text-align: right;
        line-height: 1em;
    }

    .Presentation__name-area__names__name--last {
        font-size: 8em;
        margin: 0;
        font-weight: 200;
        text-align: right;
        line-height: 1em;
    }
    
    .Presentation__name-area__names__description {
        margin: 0;
        height: 1em;
        font-size: 2em;
        text-align: right;
    }

    .Presentation__name-area__svg {
        margin-bottom: -13em;
        z-index: -1; 
        padding-left: 11%;
        width: 360px;
        height: 20px;
    }

    .Presentation__name-area__svg__rectangle {
        width: 360px;
        height: 15px;
        z-index: -1; 
    }

    .Presentation__image {
        margin: auto;
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
        margin-top: 0;
        padding-top: 0;
    }
}

@media (max-width: 480px) {

    .Presentation {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    .Presentation__name-area__names {
        width: 230px;
        margin: auto;
    }
    
    .Presentation__name-area__names__name--first {
        font-size: 5em;
        margin: 0;
        font-weight: 200;
        text-align: right;
        line-height: 1em;
    }
    .Presentation__name-area__names__name--last {
        font-size: 5em;
        margin: 0;
        font-weight: 200;
        text-align: right;
        line-height: 1em;
    }
    
    .Presentation__name-area__names__description {
        margin: 0;
        height: 1em;
        font-size: 1em;
        text-align: right;
    }

    .Presentation__name-area__svg {
        margin-bottom: -8em;
        z-index: -1; 
        padding-left: 4%;
        width: 225px;
        height: 10px;
    }

    .Presentation__name-area__svg__rectangle {
        width: 225px;
        height: 15px;
        z-index: -1; 
    }

    .Presentation__image {
        margin: auto;
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        margin-top: 0;
        padding-top: 0;
    }
}

@media (min-width: 1088px) {
    .Presentation {
        padding-left: 3%;
        padding-right: 10%;
    }
}
.Tag {
    width: 18em;
    height: 3em;
    text-align: center;
    border-radius: 0.2em;
    margin: auto;
    margin-top: 1em;
    margin-left: 0.5em;
    margin-right: 0.5em;
    border-left: #e06b6b solid;
    display: block;

    background: -webkit-gradient(linear, left top, right top, from(#8156bc),color-stop(50%, #e06b6b), color-stop(50%, #191919));

    background: linear-gradient(to right, #8156bc 0%,#e06b6b 50%, #191919 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
}

.Tag:hover {
    background-position: left bottom;
    border-left: #8156bc solid;
}

.TagName {
    margin: 1em;
    color: #cccccc;
}

